import './App.css';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import {useSelector} from "react-redux";
import {getTodosTC} from "./module/todoList-slice";
import {Button} from "./components/Button/Button";
import {AppRootStateType, useAppDispatch} from "./store";
import {ThemeType, toggleThemeAC} from "./module/app-slice";
import React, {useEffect} from "react";
import {ErrorSnackBar} from "./components/ErrorSnackBar/ErrorSnackBar";
import {Routing} from "./common/routing/Routing";

function App() {
    //    dispatch   useAppDispatch  store     
    // genericami  dispatch
    const dispatch = useAppDispatch();

    useEffect(() => {
        dispatch(getTodosTC())
    }, [dispatch]);

    const themeValue = useSelector<AppRootStateType, ThemeType>(state => state.app.theme)

    return <div className="App">
        <div className={themeValue === ThemeType.White ? 'whiteTheme' : 'blackTheme'}>
            <ErrorSnackBar/>
            <Container style={{minWidth: '1700px'}} fixed>
                <Button title={'Change theme'} className={'btnChangeTheme'}
                        onClick={() => dispatch(toggleThemeAC())}/>
                <Grid container>
                    <Routing/>
                </Grid>
            </Container>
        </div>
    </div>
}

export default App;